body {
  background-color: #ff9205;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding-bottom: 5rem; }

h1 {
  color: white;
  padding: 0 2rem; }

.container {
  position: absolute;
  overflow-x: hidden;
  left: 0;
  top: 0;
  height: 100%; }

.target {
  cursor: pointer; }
  .target__board {
    background-color: #e01818;
    width: 100px;
    height: 100px;
    margin: 12rem auto 5rem;
    border-radius: 50%;
    border: 5px solid white;
    display: flex;
    box-shadow: 0px 10px 25px -15px black; }
    .target__board h2 {
      align-self: center;
      color: white;
      text-align: center;
      width: 100%;
      pointer-events: none; }
  .target__box {
    display: inline-block;
    width: 100px;
    background-color: #e01818;
    color: white;
    font-weight: 500;
    padding: 1rem;
    text-align: center;
    border: 5px solid white;
    box-shadow: 0px 10px 25px -15px black;
    position: absolute; }
    .target__box span {
      pointer-events: none; }
    .target__box--left {
      left: 1rem; }
    .target__box--right {
      right: 1rem; }

.reference {
  background-color: white;
  width: 200px;
  height: 100px;
  border-radius: 5px;
  border: 5px solid #4e4e4e;
  display: flex;
  box-shadow: 0px 10px 25px -15px black;
  cursor: pointer; }
  .reference h2 {
    color: #4e4e4e;
    align-self: center;
    text-align: center;
    width: 100%; }

p {
  font-weight: 500;
  color: #e0e0e0;
  width: 90%;
  margin: 5rem auto 10rem;
  text-shadow: 1px 1px 0px #e01818; }
  p a {
    color: white;
    font-weight: bold; }
